import $C from './config.js';
import $store from '../store/index.js'
export default {
	common :{
		method:"GET",
		header:{
			"content-type":"application/json"
		},
		data:{}
	},
	request(options = {})
	{
		options.url = $C.webUrl + options.url
		options.method = options.method || this.common.method
		options.header = options.header || this.common.header
		if(options.token){
			options.header.token = $store.state.token
			if(!options.noCheck && !options.header.token && !options.notoast){
				return uni.showToast({
					title: 'token,',
					icon: 'none'
				});
			}
		}
		return new Promise ((resolve,reject)=>{
			uni.request({
				...options,
				success: (res) => {
					if(options.native) return resolve(res)
					if(res.statusCode != 200 && !options.notoast)
					{
						uni.showToast({
							icon:'none',
							title:res.data.msg || ''
						})
						
						return reject(res.data)
					}
					return resolve(res.data.data || res)
				},
				fail: (err) => {
					uni.showToast({
						icon:'none',
						title:err.message || ''
					})
					return reject('')
				}
			})
		})
		
	},
	get(url,data = {},options = {}) {
			options.url = url
			options.data = data
			options.method = 'GET'
			return this.request(options)
	},
	post(url,data = {},options = {}) {
			options.url = url
			options.data = data
			options.method = 'POST'
			return this.request(options)
	},
	upload(url,options = {}){
		options.url = $C.webUrl + url
		options.header = options.header || {}
		// token
		if(options.token){
			options.header.token = $store.state.token
			if(!options.header.token){
				return uni.showToast({
					title: 'token,',
					icon: 'none'
				});
			}
		}
		
		return new Promise((res,rej)=>{
			uni.uploadFile({
				...options,
				success: (uploadFileRes) => {
					if(uploadFileRes.statusCode !== 200){
						return uni.showToast({
							title: '',
							icon: 'none'
						});
					}
					let data = JSON.parse(uploadFileRes.data)
					res(data)
				},
				fail:(err)=>{
					rej(err)
				}
			});
		})
		
	}
	
	
}